body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.main-home {
  justify-self: end;
  line-height: 2.5rem;
  height: 2.5rem;
  font-size: 1.5rem;
  width: 10%;
  text-align: center;
  color: #fff;
  font-weight: 700;
  position: relative; }

.main-score {
  justify-self: center;
  line-height: 2.5rem;
  opacity: 0;
  width: 80%;
  height: 2.5rem;
  font-size: 2rem;
  text-align: center;
  color: #fff;
  font-weight: 700;
  -webkit-transform: none;
          transform: none; }

.main-away {
  justify-self: start;
  line-height: 2.5rem;
  height: 2.5rem;
  font-size: 1.5rem;
  text-align: center;
  color: #000;
  width: 10%;
  font-weight: 700;
  position: relative; }

.main-time {
  justify-self: start;
  line-height: 2.5rem;
  height: 2.5rem;
  font-size: 1.5rem;
  width: 10%;
  text-align: center;
  color: #fff;
  font-weight: 700; }

.border-home {
  height: 2.5rem;
  position: absolute;
  right: 0px; }

.border-away {
  height: 2.5rem;
  position: absolute;
  left: 0px; }

.team-stat {
  width: 50%;
  text-overflow: ellipsis;
  white-space: nowrap;
  height: 2.5rem;
  line-height: 2.5rem;
  font-size: 1.5rem;
  text-align: center;
  color: #fff;
  font-weight: 700; }

.info-stat {
  width: 50%;
  text-overflow: ellipsis;
  white-space: nowrap;
  height: 2.5rem;
  line-height: 2.5rem;
  font-size: 1.25rem;
  text-align: center;
  color: #fff;
  font-weight: 500; }

.main-content {
  width: 70vw;
  height: 70vh;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -35vw;
  margin-top: -35vh; }

.score-board {
  margin: 50px auto 50px 50px;
  height: 300px; }

.my-button {
  display: inline-block;
  border: solid 2px;
  padding: 12px;
  border-radius: 30px;
  border-color: #d44b52;
  background: #fff;
  color: #d44b52;
  margin: 2px 5px 2px 5px;
  box-shadow: 0 6px 14px 0 rgba(80, 80, 80, 0.33);
  font-weight: 500; }
  .my-button:hover {
    cursor: pointer;
    background: #d44b52;
    color: #fff; }

.panel {
  width: 100%;
  background: #fff;
  opacity: 0.9;
  position: absolute;
  bottom: 0;
  padding: 20px; }

.button-group {
  display: flex;
  justify-content: center;
  align-items: center; }

.form-group {
  display: flex;
  justify-content: center;
  align-items: center; }
  .form-group span {
    font-size: 18px;
    font-weight: 600; }

.board-grid {
  width: 900px;
  display: grid;
  grid-template-columns: 12% 12% 12% 33.33% 16.67% 14%; }

input {
  outline-style: none;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 7px 7px;
  margin: 10px; }

/* customized raido */
.radio {
  margin: 1rem; }
  .radio input[type="radio"] {
    position: absolute;
    opacity: 0; }
    .radio input[type="radio"] + .radio-label:before {
      content: "";
      background: #f4f4f4;
      border-radius: 100%;
      border: 1px solid #b4b4b4;
      display: inline-block;
      width: 1rem;
      height: 1rem;
      margin-right: 1rem;
      margin-top: 2rem;
      vertical-align: center;
      cursor: pointer;
      text-align: center; }
    .radio input[type="radio"]:checked + .radio-label:before {
      background-color: #d44b52;
      box-shadow: inset 0 0 0 4px #f4f4f4; }
    .radio input[type="radio"]:focus + .radio-label:before {
      outline: none;
      border-color: #d44b52; }
    .radio input[type="radio"]:disabled + .radio-label:before {
      box-shadow: inset 0 0 0 4px #f4f4f4;
      border-color: #b4b4b4;
      background: #b4b4b4; }
    .radio input[type="radio"] + .radio-label:empty:before {
      margin-right: 0; }

